.page-footer {
  padding-top: 58px;
  padding-bottom: 158px;
  margin-top: auto;

  @include mw(768) {
    padding-bottom: 80px; }

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include mw(768) {
      flex-direction: column-reverse; } }

  &__col {}

  &__col--left {
    display: flex;
    align-items: center;

    @include mw(768) {
      justify-content: space-between;
      width: 100%; } }

  &__copy {
    font-weight: 500;
    font-size: rem(14);
    line-height: rem(16); }

  &__pay {
    display: flex;
    align-items: center;
    margin-left: 66px; }

  &__col--right {

    @include mw(768) {
      margin-bottom: 40px;
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: flex-start; } }

  &__link {
    font-weight: 500;
    font-size: rem(14);
    line-height: rem(16);
    color: #8D8D93;

    & + a {
      margin-left: 50px;

      @include mw(768) {
        margin-left: 0;
        margin-top: 30px; } } } }

.container {}

.pay {

  &__item {

    & + li {
      margin-left: 23px; } }

  &__item--visa {}

  &__img {
    display: block; }

  &__item--mir {}

  &__item--mc {} }
